<div class="color-square dropdown" :style="{backgroundColor: themeColor.color.fg, borderColor: themeColor.color.bg}"><div class="dropdown-content"><div class="color-square" v-for="color, index in themeColors" :style="{backgroundColor: color.fg, borderColor: color.bg}" @click="setThemeColor(index)" :class="{active: color.fg === themeColor.color.fg }"><div class="inner-square" :style="{backgroundColor: color.sc}"></div></div></div></div>